.tb_gt_post_in {
    display: block;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_gt_media_wrap {
    position: relative;
}

.tb_gt_post_wrapper {
    padding: 3px;
}

.tb_gt_post_details,
.tb_gt_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    opacity: 0;
    z-index: 1;
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: opacity .3s ease, visibility .3s ease;
}

.tb_gt_post_overlay {
    z-index: 3;
}

.tb_gt_post_overlay-light {
    background-color: rgba(255, 255, 255, .3);
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .4) 100%);
}

.tb_gt_post_overlay-dark {
    background-color: rgba(0, 0, 0, .2);
    background-image: linear-gradient(135deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .4) 100%);
}

.tb_gt_post_details {
    z-index: 4;
}

@media only screen and (min-width: 991px) {

    .tb_gt_post_in:hover .tb_gt_post_overlay,
    .tb_gt_post_in:hover .tb_gt_post_details {
        opacity: 1;
        visibility: visible;
    }

    .tb_gt_post_ani:hover .tb_gt_post_details {
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }

    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }

        100% {
            transform: translateY(0);
        }
    }

    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }

        100% {
            transform: translateY(0);
        }
    }

    .tb_gt_post_ani:hover .tb__media_ico_ .tb__icon,
    .tb_gt_post_ani:hover .tb__media_ico_ .tb__icon {
        opacity: 0;
        transform: scaleX(.4) scaleY(.4) translateZ(0);
    }
}

.tb_gt_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

.tb_gt_image_wrap_{
    position: relative;
    padding-bottom: 100%;
    background-color: rgba(0,0,0,.03);
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_gt_image_wrap_in {
    position: absolute;
    height: 100%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
}


.tb_gt_image {
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_gt_image_wrap_ img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.tb_gt_image, .tb_gt_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_gt_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_gt_video iframe{
    pointer-events: none;
}


.tb_blur_bg_:after {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, .35);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.tb_blur_bg_ {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    height: 100%;
    margin: 0 auto;
    left: auto;
    right: auto;
    top: 0;
    width: 100%;
}

.tb_gt_image_wrap_ img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}


.tb_gt_social_ {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0;
}

.tb_gt_post_sm .tb_gt_social_ {
    margin: 5px 0;
}

.tb_gt_post_sm .tb_gt_btn_wrap {
    padding: 10px;
}

.tb_gt_social_ico_ {
    font-size: 22px;
}

.tb_gt_author {
    width: calc(100% - 30px);
}


.tb_gt_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0, 0, 0, .2);
    overflow: hidden;
}

.tb_gt_author_profile__ {
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_gt_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-align: left;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_gt_authorname,
.tb_gt_username,
.tb_gt_time {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_gt_seprator {
    background-color: var(--tb-author-color);
}

.tb_gt_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_gt_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_gt_author_wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 10px;
}

.tb_gt_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_gt_post_info {
    display: flex;
    align-items: center;
}

.tb_gt_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_gt_username,
.tb_gt_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_gt_username {
    max-width: 65%;
}

.tb_gt_time {
    max-width: 35%;
}

.tb_gt_social__ico {
    font-size: 22px;
    color: var(--tb-icon-color);
}

.tb_gt_post_sm .tb_gt_social__ico {
    font-size: 18px;
}

.tb_gt_post_sm {
    font-size: 18px;
}

.tb_gt_social_top_ {
    position: absolute;
    z-index: 4;
    right: 10px;
    top: 10px;
}

.tb_gt_content {
    word-break: break-word;
    white-space: pre-line;
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis;
    width: 100%;
}

.tb_gt_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_gt_share_container {
    z-index: 4;
}

.tb_gt_post_in:hover .tb_gt_play_ico {
    opacity: .5;
    transition-property: opacity;
    transition-timing-function: ease-in;
    transition-duration: .3s;
}

.tb_gt_video_ico,
.tb_gt_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 2;
    color: #fff;
    font-size: 12px;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .2));
}

.tb_gt_video_ico {
    font-size: 12px;
}

.tb_gt_audio_ico {
    font-size: 16px;
}

.tb_gt_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .2));
}

.tb_gt_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .2));
}

.tb_gt_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_gt_post_wrapper2x .tb_gt_author_profile {
    height: 30px;
    width: 30px;
    margin-right: 5px;
}

.tb_gt_post_wrapper2x .tb_gt_authorname {
    font-size: 10px;
    margin-bottom: 0px;
}

.tb_gt_post_wrapper2x .tb_gt_post_info,
.tb_gt_post_sm .tb_gt_post_wrapper2x .tb_shop_btn_wrap {
    display: none;
}

.tb_gt_post_wrapper2x .tb_gt_contant_wrapper {
    padding: 7px;
}

.tb_gt_post_wrapper2x .tb_gt_content {
    -webkit-line-clamp: 2;
}

.tb_gt_post_wrapper2x .tb_gt_author_info {
    padding-right: 10px;
    width: calc(100% - 30px);
}

.tb_gt_post_wrapper2x .tb_gt_author_wrapper {
    padding: 5px;
}

.tb_gt_media_icon .tb_multi_images_ico {
    right: auto;
    left: 12px;
    z-index: 3;
}

.tb_gt_arrow {
    position: absolute;
    top: calc(50% - 23px);
    color: #000;
    background-color: #fff;
    border-radius: 35px;
    height: 46px;
    width: 46px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    z-index: 9;
    cursor: pointer;
    -webkit-transform: translateY(0) !important;
    transform: translateY(0) !important;
}

.tb_gt_arrow:hover {
    opacity: 1;
}

.tb_gt_arrow_left__ {
    left: 10px;
    right: auto;
    box-shadow: 3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_gt_arrow_right__ {
    right: 10px;
    left: auto;
    box-shadow: -3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_gt_post_sm .tb__media_ico_ .tb__icon:nth-child(1) {
    left: auto;
    right: 6px;
    top: 6px;
}

.tb_gt_post_sm .tb__media_ico_ .tb__icon:nth-child(2) {
    left: 6px;
    right: auto;
    top: 6px;
}

.tb_gt_post_sm .tb__media_ico_ .tb__icon:nth-child(3) {
    left: 6px;
    right: auto;
    top: auto;
    bottom: 6px;
}

.tb_gt_post_sm .tb_multiple_ico,
.tb_gt_post_sm .tb_shop_ico,
.tb_gt_post_sm .tb_audio_ico {
    font-size: 12px;
}

.tb_gt_post_sm .tb_video_ico {
    font-size: 10px;
}

.tb_gt_post_row {
    display: flex;
    align-items: start;
    flex-wrap: wrap;
    width: 100%;
}

.tb_gt_post_sm {
    width: 33.33%;
    display: flex;
    flex-direction: column;
}

.tb_gt_post_lg {
    width: 66.66%;
}

.tb_gt_post_in .tb_onsite_upload_btn{
    border-radius: 0;
}

.tb_gt_post_in .tb_onsite_btn_txt{
    flex-grow: 0;
}

.tb_gt_post_in .tb_onsite_btn_txt {
    font-size: 14px;
}

.tb_gt_post_in-2x .tb_onsite_btn_txt {
    font-size: 12px;
}

.tb_gt_post_in-1x .tb_onsite_qr_code {
    margin-bottom: 0;
    width: 100%;
    height: 100%;
}

.tb_gt_post_in-1x .tb_onsite_qr_code svg {
    max-width: 80%;
}

.tb_gt_post_in-1x .tb_onsite_btn_txt {
    font-size: 0px;
}

.tb_gt_post_in-1x .tb_onsite_btn_txt:before {
    content: 'Add Your Photo';
    font-size: 12px;
    color: var(--tb-snap-color);
}

.tb_gt_post_in-1x .tb_onsite_qr_code {
    height: auto;
}

.tb_gt_post_in-1x .tb_onsite_qr_code:before {
    content: "\e92d";
    font-family: 'WebSocialFonts' !important;
    font-size: 26px;
}

.tb_onsite_post__ .tb_gt_post_in-1x .tb_onsite_qr_code svg {
    display: none;
}

.tb_gt_post_sm .tb_shop_btn{
    padding: 9px 11px;
    font-size: 11px;
}

@media screen and (max-width: 991px) {
    .tb_gt_arrow {
        top: calc(50% - 20px);
        height: 40px;
        width: 40px;
        font-size: 16px;
    }
}